<template>
    <transition name="slider">
    <div class="upload-confirm-wrap">
      <div class="confirm-img">
        <img v-bind:src="imgSrc" alt="">
      </div>
      <div class="confirm-btn-wrap">
        <span class="upload-back" @click="back"></span>
        <span class="upload-confirm-btn" @click="_handleImg"></span>
      </div>
    </div>
    </transition>
</template>

<script>
  // import {doMain} from  'api/url'
  import axios from 'common/js/http';
  import {mapGetters,mapMutations} from 'vuex'
    export default {
       name: "",
       props:{
          imgSrc:{
            default: ''
          },
          options:{
            default: ''
          }
       },
      data(){
         return{

         }
      },
      computed:{
        ...mapGetters([
          'loadingOnOff',
        ])
      },
      methods:{
        back(){
            this.$emit('select')
        },
        _handleImg(){
          let ops = this.options;
          // http
          axios.interceptors.request.use(config => {
              this.setLoadingOnOff(true)
            return config
          }, error => {
            this.setLoadingOnOff(false)
            return Promise.reject(error)
          });
          axios.interceptors.response.use(
            response => {
              this.setLoadingOnOff(false)
              return response;
            },
            error => {
              this.setLoadingOnOff(false)
              return Promise.reject(error.response.data)
            });

          axios.get(process.env.API_HOST+'rolleye/facefuse/index',{
            params:{
              pic:ops.imgSrc,
              tips : 76,
              uploadTimer:ops.uploadTimer,
            }
            // createCanvasTimer:ops.createCanvasTimer
          },{withCredentials: true}).then((res)=>{
            // console.log(res)
            this.setLoadingOnOff(false)
            if(res.data.rs===0){
                alert(res.data.message)
            }else{
              this.$router.push({
                path:'result',
                query:{
                  imgsrc:encodeURIComponent(res.data.pic)
                }
              })
            }
          })
        },
        ...mapMutations({
          setLoadingOnOff:'SET_LOADING'
        })
      }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
.upload-confirm-wrap{
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   z-index: 30;
   background: #fff;
}
.confirm-img{
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  img{
    width: 100%;
    height: auto;
  }
}
.confirm-btn-wrap{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4.453333125rem;
  background: url("../common/image/upload-confirm-bg.jpg") no-repeat;
  background-size: 100% auto;
  span{
    display: inline-block;
  }
}
.upload-back{
  position: absolute;
  top: 2.1rem;
  left: 0.7rem;
  width: 0.8rem;
  height: 0.8rem;
  background: url("../common/image/sprite.icons.png") no-repeat;
  background-position: -4.08rem -8.42rem;
  background-size: 941px auto;
}
.upload-confirm-btn{
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 1.95rem;
  height: 1.96rem;
  background: url("../common/image/sprite.icons.png") no-repeat;
  background-position: 0 -8.4133333rem;
  background-size: 941px auto;
}
.slider-enter-active, .slider-leave-active{
  transition: all 0.4s
}
.slider-enter, .slider-leave-to{
  transform: translate3d(100%, 0, 0)
}
</style>



